import { mapActions, mapGetters } from "vuex";

export default {
  name: "DrawInteraction",
  props: {
    source: String
  },
  computed: {
    ...mapGetters(["getBrush", "getDrawnFeatures"]),
    features: {
      get() {
        return this.getDrawnFeatures;
      },
      set(val) {
        this.setDrawnFeatures(val);
      }
    }
  },
  methods: {
    ...mapActions(["setDrawnFetaures"])
  }
};
