import { mapGetters } from "vuex";

export default {
  name: "DrawLayer",
  props: {
    sourceName: String,
    value: Array,
    visible: Boolean,
    zIndex: Number
  },
  computed: {
    ...mapGetters(["getDrawLayerVisibility"]),
    features: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("value", val);
      }
    }
  }
};
